@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
/* Font style rules */
/* font-family: 'Nunito', sans-serif;
font-family: 'Prompt', sans-serif;
font-family: 'Titillium Web', sans-serif; */

* {
  box-sizing: border-box;
}

/* html {
  font-family: 'Noto Sans JP', sans-serif;
  
} */

body {
  margin: 0;
  /* background-color: #3f3f3f; */
  background-image: linear-gradient(to right, #3d39ad, #9b9b9b);
  text-align: center;
  font-family: 'Nunito', sans-serif;
}

.wrapper {
  margin-right: auto; /* 1 */
  margin-left: auto; /* 1 */
  }

h1 {
  color: white;
  font-size: 2.5rem;
}

footer {
  height: 8rem;
}

.main-logo{
  margin-top: 2rem;

}

@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2) {
      .main-logo{
        width: 60%;
        height: 60%;
      }
      

}
p {
    color: white;
}

.Card {
    object-fit: contain;
}

.logo-pic img {
    height: 100%;
    opacity: 70%;
    border-radius: 8px;
    box-shadow: 0px 10px 5px #1e1f4a;
    justify-content: center;
}

.logo-pic:active {

    box-shadow: 0 5px #666;
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
}


.link-title {
    font-size: 1.3rem;
    color: black;
    text-shadow: 2rem;
    -webkit-animation-name: hover-animate;
            animation-name: hover-animate;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    word-break: keep-all;
    text-shadow: 1px 1px 8px #1f1e29;
}

a:hover {
    -webkit-animation-name: fade-in;
            animation-name: fade-in;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

img:hover {
    opacity: 100%;
}



@-webkit-keyframes fade-in {
    from {
        color: black;
    }

    to {
        color: white;
    }
}



@keyframes fade-in {
    from {
        color: black;
    }

    to {
        color: white;
    }
}



@media only screen and (min-device-width: 320px) and (max-device-width: 480px) and (-webkit-min-device-pixel-ratio: 2) {
    .container {
        font-size: 1rem;
    }

}
.card{
    text-align: center;
    border-radius: 12px;
    border-width: 2px;
    border-style: outset;
    border-color: white;
    box-shadow: 2rem;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
    margin: 3rem;
    width: 40%;
    margin: auto;
    /* background-image: linear-gradient(to right, #3d39ad, #457ac4); */
    margin-top: 2rem;
    padding: 2rem;
}



@media only screen and (min-device-width: 320px) and (max-device-width: 480px) and (-webkit-min-device-pixel-ratio: 2) {
    .card{
        width: 80%;
    }

}
